import type { BiFactory } from '@ali/4ever-cangjie';
import renderContent from './renderContent';
import { actions, isActions } from './actions';
import createRenderNode from './createRenderNode';
import onAction from './handlers/onAction';
import toolbar from './toolbar';
import renderLeftToolbar from './components/dragHander';
import { leftToolbarHandlers, handleDragChange } from './leftToolbarHandlers';
import { queries } from './queries';
import { key } from '../utils/meta';
import { commands } from './commands';

export const biFactory: BiFactory = (configs) => {
  return {
    key,
    renderContent: renderContent(configs, handleDragChange),
    renderNode: createRenderNode(configs),
    queries: {
      ...queries,
      /**
       * TODO: 获取 configs 不应该是个 query，待确定历史原因
       */
      getDraggableConfig: () => {
        return configs.draggable;
      },
    },
    onAction,
    actions, 
    isActions,
    commands,
    models: [],
    toolbar: toolbar(configs),
    leftToolbar: renderLeftToolbar(configs),
    leftToolbarHandlers,
  };
};
